body{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
} 
.navbar{
  background-color: #fff;
}
.nav_bg{
  background-color: #fff;
}
.navbar-brand{
  font-size: 1.9rem;
  color: #565387;
}

.navbar a{
  font-size: 1.3rem;
  text-transform: capitalize;
}
.menu_active{
  font-weight: bold;
  border-bottom: 1px solid #565387;
}

#header{
  width: 100%;
  height: 80vh;
  margin-top: 20px;
}

#header h1{
  margin: 0 0 10px 0;
  font-size: 42px;
  font-weight: 700;
  color: black;
}

#header .brand-name{
  color: #3498db;
}

#header h2{
  color: #484848;
  font-size: 22px;
}
#header .btn-get-started{
  font-weight: 500;
  font-size: 16px;
  letter-spacing: 1px;
  display: inline-block;
  padding: 10px 30px;
  border: 1px solid #3498db;
  border-radius: 50px;
  transition: 0.5s;
  color: #3498db;
  text-decoration: none;
}

#header .btn-get-started:hover{
  background: #3498db;
  color: #fff;
}

.header-img img{
  width: auto;
  height: auto;
  text-align: right;
}

#header .animated{
  animation: up-down 2s ease-in-out infinite alternate-reverse both;
}

.card{
  width: 300px;
  height: auto;
}
.card img{
  width: 300px;
  height: 250px;
}

@-webkit-keyframes up-down{
  0%{
      transform: translateY(10px);
  }
  100%{
      transform: translateY(-10px);
  }
}

@keyframes up-down{
  0%{
      transform: translateY(10px);
  }
  100%{
      transform: translateY(-10px);
  }
}

@media (max-width:991px){
  #header{
      height: 80vh;
      text-align: center;
  }
  
  #header .header-img{
      text-align: center;
  }

  #header .header-img img{
      width: 60%;
  }
}

@media (max-width:768px){
  #header{
      margin-top: 20px;
  }
  #header h1{
      font-size: 28px;
  }
  #header h2{
      font-size: 18px;
      margin-bottom: 30px;
  }
  #header .header-img img{
      width: 70%;
  }
  .card{
      width: 250px;
      height: auto;
      margin-left: -20px;
  }
  .card img{
      width: 250px;
      height: 150px;
  }
}